@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
body, html {
  height: 100%;
  margin:0;
  cursor:url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'  width='45' height='54' viewport='0 0 100 100' style='fill:black;font-size:27px;'><text y='50%'>⛄</text></svg>") 16 0,auto;
}

.frontpage{
  padding: 0px 50px;
  background-image: url(/pod-portfolio/static/media/background1.e9ef41d4.gif);
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  color: #ddf2f3;
 
  height: 100vh;
}

.container h1{
margin-bottom: 200px;
text-align: center;
font-weight: 800;
font-size: 60px;
font-family: 'Architects Daughter', cursive;
}

.container img{
  transition: .4s ease-in-out;
  -webkit-filter: invert(0) blur(3px);
          filter: invert(0) blur(3px);
  top: 100px;
  max-width: 25%;
}
.container img:hover{
  animation-fill-mode: forwards;
  -webkit-filter: invert(100%) blur(0);
          filter: invert(100%) blur(0);
}
body {
    background: rgb(62, 236, 248);  
  }
  
  .left {
    position: absolute;
    left: 500px;
  }
 
  .right {
    right: 20px;
  }
  
  .center {
    text-align: center;
  }
  
  .bottom {
    position: absolute;
    bottom: 8px;
  }
  
  #card {
    position: relative;
    width: 100%;
    height: 200px;
    padding: 15px;
    margin-top:15px;
    margin-bottom:25px;
    background: #E9E2D0;
    box-shadow: -20px 0 35px -25px black, 10px 0 35px -25px black;
    z-index: 5;
  }
  
  #card img {
    height:100%;
    width:30%;
    float: left;
    border-radius: 5px;
    margin-right: 20px;
    -webkit-filter: blur(2px);
            filter: blur(2px);
    transition: .3s ease-in-out;
  }

  #card img:hover{
    -webkit-filter: blur(0);
            filter: blur(0);
  } 

  #card h2 {
    font-family: courier;
    color: #333;
    margin: 0 auto;
    padding: 0;
    font-size: 20pt;
  }
  
  #card p {
    font-family: courier;
    color: #555;
    font-size: 15px;
  }
  
  #card span {
    font-family: courier;
  }
a{
  text-decoration: none;
}

